import React from "react";
import PropTypes from 'prop-types';
// import { Link } from "react-router-dom";

export default function Navbar(props) {
  return (
    <div>
      <nav className={`navbar navbar-expand-lg navbar-${props.mode === 'light' ? 'light' : 'dark'} bg-${props.mode === 'light' ? 'light' : 'dark'}`}>
        <div className="container-fluid">
          <a className="navbar-brand" href="/">{props.title}</a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              {/* <li className="nav-item">
                <Link className="nav-link" aria-current="page" to="/"> Home </Link>
              </li>
              <li className="nav-item" >
                <Link className="nav-link" to="/about"> About </Link>
              </li> */}
            </ul>
            <div className="form-check mx-1">
              <input className="form-check-input" type="radio" name="exampleRadios" id="dark" value="1" onClick={props.handleMode} />
              <label className="form-check-label" htmlFor="exampleRadios1">
                Dark
              </label>
            </div>
            <div className="form-check mx-1">
              <input className="form-check-input" type="radio" name="exampleRadios" id="blue" value="2" onClick={props.handleMode} />
              <label className="form-check-label" htmlFor="exampleRadios2">
                Blue
              </label>
            </div>
            <div className="form-check mx-1">
              <input className="form-check-input" type="radio" name="exampleRadios" id="light" value="3" onClick={props.handleMode} />
              <label className="form-check-label" htmlFor="exampleRadios3">
                Light
              </label>
            </div>
          </div>
        </div>
      </nav>
    </div>
  )
}
Navbar.defaultProps = {
  title: "Set title here"
};
Navbar.propTypes = { title: PropTypes.string.isRequired };

