import React, { useState } from "react";
import '../App.css';

export default function TextForm(props) {
  const [Text, setText] = useState("");
  const [font, setFont] = useState('');
  let theme = {
    backgroundColor: props.mode === 'blue' ? 'rgb(1 19 38)' : props.mode === 'dark' ? 'rgb(33 37 41)' : 'white',
    color: props.mode === 'blue' ? 'white' : props.mode === 'dark' ? 'white' : 'black',
    fontFamily: font
  };

  const handleChange = (event) => {
    setText(event.target.value);
  };

  const Upcase = () => {
    setText(Text.toUpperCase());
    props.showAlert("Converted to Uppercase !!")
  };

  const downcase = () => {
    setText(Text.toLowerCase());
    props.showAlert("Converted to Lowercase !!")
  };

  const clear = () => {
    setText("");
    props.showAlert("Text CLeared !!")
  };

  const fonts = (event) => {
    // console.log(font);
    console.log(event.target.value);
    if (event.target.value === '1')
      setFont('Roboto');
    else if (event.target.value === '2')
      setFont('Courier');
    else if (event.target.value === '3')
      setFont('Dancing Script');
    else if (event.target.value === '4')
      setFont('Lobster');
    else if (event.target.value === '5')
      setFont('Times New Roman');
  }

  const handleCopy = () => {
    navigator.clipboard.writeText(Text);
    props.showAlert("Copied to ClipBoard !!")
  }

  const handleExtraSpaces = () => {
    let newText = Text.split(/[ ]+/);
    setText(newText.join(" "))
    props.showAlert("Extra Spaces Removed !!")
  }


  return (
    <>
      <div>
        <h2 className="my-2">{props.heading}</h2>
        <div className="mb-3">
          <textarea className="form-control my-2" id="text" rows="8" value={Text} onChange={handleChange} style={theme}></textarea>
          <button className={`btn btn-${props.mode === 'dark' ? 'dark' : props.mode === 'blue' ? 'primary' : 'success'} btn-sm mx-1 my-1`} onClick={Upcase} disabled={Text.split(" ").filter((e) => { return e.length !== 0 }).length === 0}>Uppercase</button>
          <button className={`btn btn-${props.mode === 'dark' ? 'dark' : props.mode === 'blue' ? 'primary' : 'success'} btn-sm mx-1 my-1`} onClick={downcase} disabled={Text.split(" ").filter((e) => { return e.length !== 0 }).length === 0}>Lowercase</button>
          <button className={`btn btn-${props.mode === 'dark' ? 'dark' : props.mode === 'blue' ? 'primary' : 'success'} btn-sm mx-1 my-1`} onClick={handleCopy} disabled={Text.split(" ").filter((e) => { return e.length !== 0 }).length === 0}> Copy Text</button>
          <button className={`btn btn-${props.mode === 'dark' ? 'dark' : props.mode === 'blue' ? 'primary' : 'success'} btn-sm mx-1 my-1`} onClick={clear} disabled={Text.split(" ").filter((e) => { return e.length !== 0 }).length === 0}>Clear All</button>
          <button className={`btn btn-${props.mode === 'dark' ? 'dark' : props.mode === 'blue' ? 'primary' : 'success'} btn-sm mx-1 my-1`} onClick={handleExtraSpaces} disabled={Text.length === 0}>Remove extra spaces</button>
        </div>
        <div className="container">
          <select className="form-select" aria-label="Default select example" style={theme} onChange={fonts}>
            <option value="0"> Select Fonts </option>
            <option value="1"> Roboto </option>
            <option value="2"> Courier </option>
            <option value="3"> Dancing Script </option>
            <option value="4"> Lobster </option>
            <option value="5"> Times New Roman </option>
          </select>
        </div>
      </div>
      <div className="container my-2">
        <h2>Your text Summary</h2>
        <span>
          {Text.split(/\s+/).filter((e) => { return e.length !== 0 }).length} words and {Text.length} characters
        </span>
        <p>{0.008 * Text.split(/\s+/).filter((e) => { return e.length !== 0 }).length} minutes read</p>
      </div>
      <div className="container">
        <h3>Preview</h3>
        <p style={{ fontFamily: font }}>{Text || "Enter Something to Preview"}</p>
      </div>
    </>
  );
}
