import './App.css';
import { useState } from 'react';
import Navbar from './my_components/Navbar';
import TextForm from './my_components/TextForm';
import Alert from './my_components/Alert';
// import About from './my_components/About'
import React from "react";
// import {
//   BrowserRouter as Router,
//   Switch,
//   Route
// } from "react-router-dom";

function App() {
  const [Mode, setMode] = useState('light');
  const [alert, setAlert] = useState(null);

  const handleMode = () => {
    if (document.getElementById('blue').checked === true) {
      setMode('blue');
      document.body.style.backgroundColor = 'rgb(3 0 23)'
      document.body.style.color = 'white'
      showAlert("Blue Theme Enabled")
    }
    else if (document.getElementById('dark').checked === true) {
      setMode('dark');
      document.body.style.backgroundColor = 'black'
      document.body.style.color = 'white'
      showAlert("Dark Theme Enabled")
    }
    else if (document.getElementById('light').checked === true) {
      setMode('light');
      document.body.style.backgroundColor = '#e3f2fd'
      document.body.style.color = 'rgb(3 0 23)'
      showAlert("Light Theme Enabled")
    }
  }

  const showAlert = (msg) => {
    setAlert(msg);
    setTimeout(() => {
      setAlert(null);
    }, 1500);
  }

  return (
    <>
      <Navbar title="TextUtils" mode={Mode} handleMode={handleMode} />
      <Alert alert={alert} />
      <div className="container">
      <TextForm heading="Try TextUtils - word counter, character counter, remove extra spaces" mode={Mode} showAlert={showAlert} />
      </div>
    </>
    // WITH REACT ROUTER
    // <Router>
    //   <Navbar title="TextUtils" mode={Mode} handleMode={handleMode} />
    //   <Alert alert={alert} />
    //   <div className="container">
    //     <Switch>
    //       <Route exact path="/about">
    //         <About mode={Mode}/>
    //       </Route>
    //       <Route exact path="/">
    //         <TextForm heading="Try TextUtils - word counter, character counter, remove extra spaces" mode={Mode} showAlert={showAlert} />
    //       </Route>
    //     </Switch>
    //   </div>
    // </Router>
  );
}

export default App;
